<template>
	<view class="content">
		<view :style="{height: `${statusBarHeight}px`}"></view>
		<!-- 头部搜索框 -->
		<hed-serch></hed-serch>
		<!-- 轮播图组件 -->
		<my-swiper :swiperList="swiperList"></my-swiper>
		<!-- 导航组件 -->
		<my-nav-bar></my-nav-bar>
		<!-- 推荐喜欢组件 -->
		<my-like-books :NavBooks="NavBooks"></my-like-books>
		<!-- book图文混排组件 -->
		<view class="dataBox">
			<block v-for="(item,index) in booksList" :key="index">
			<view class="flex align-center justify-between p-2 my-1">
				<view class="font-lg">
					{{item.headerTitle}}
				</view>
				<view class="flex align-center">
					<view class="font text-light-black">
						查看全部
					</view>
					<my-icon iconName="icon-iconfonti" iconColor="text-light-black" iconSize="44"></my-icon>
				</view>
			</view>
			<block v-for="(ntm,ind) in item.books" :key="ntm.id">
			<view class="flex align-center p-1 border-bottom" style="height: 352rpx;">
				<image class="rounded mx-2" style="width: 276rpx; height: 276rpx;" :src="ntm.imgurl" mode=""></image>
				<view class="flex-1" style="height: 100%;">
					<view class="py-2 font-md font-weight-bold">
						{{ntm.name}}
					</view>
					<view class="font text-light-black">
						{{ntm.synopsis}}
					</view>
				</view>
				
			</view>
		</block>
		
		</block>
		</view>
	</view>
</template>

<script>
	//导入api接口
	import {
		IndexModel
	} from '../../model/index.js'
	const IndexAPi = new IndexModel()
	// 头部搜索框
	import HedSerch from '../../components/HedSerch'
	//轮播图组件
	import MySwiper from '../../components/HedSwiper'
	//导航组件
	import MyNavBar from '../../components/NavBar'
	//猜你喜欢组件
	import MyLikeBooks from '../../components/hedBooks'
	export default {
		data() {
			return {
				statusBarHeight: this.$statusBarHeight,
				swiperList: [{
						url: 'http://ceshi.dishait.cn/ceshiData/swiperImages/swipertab1.png'
					},
					{
						url: 'http://ceshi.dishait.cn/ceshiData/swiperImages/swipertab2.png'
					},
					{
						url: 'http://ceshi.dishait.cn/ceshiData/swiperImages/swipertab3.png'
					},
					{
						url: 'http://ceshi.dishait.cn/ceshiData/swiperImages/swipertab4.png'
					},
				],
				NavBooks: [],
				booksList:[]

			}
		},
		components: {
			HedSerch, //搜索组件
			MySwiper, //轮播图组件
			MyNavBar, //导航组件
			MyLikeBooks, //猜你喜欢组件
		},
		onLoad() {

		},

		methods: {
			//请求页面渲染数据
			async gitData() {
				const {
					data
				} = await IndexAPi.GitIndexData()
				//导航数组
				this.NavBooks = data.Rebooks
				//图文混排
				this.booksList = data.bookResources
				console.log(data)
			}
		},
		onShow() {
			//当页面打开时请求数据
			this.gitData()
		}
	}
</script>

<style>

</style>
